.cbt_marketplace_container
{
    float: none;
    // width: 100%;
    min-height: 100px;
    margin-right: -45px;
    > .filtr-item
    {
        display: inline-block;
        width: 33%;
        padding: 0 35px 35px 0;
        box-sizing: border-box;
        left: 0;
        .theme { 
            float: none; 
            margin: 0;
            width: 100% !important;
        }



        &.st-template-locked .theme
        {
            cursor: not-allowed;
        }
    }

    .st-marketplace-theme-status
    {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 10px;
        background: fade(#000, 80%);
        padding: 3px 7px;
        border-bottom-right-radius: 4px;
        color: #fff;
        i { color: #fdc52d; }
    }
}

.st-marketplace-filters
{
    margin: 0 0 8px;

    .st-marketplace-categories,
    .st-marketplace-search
    {
        display: inline-block;
        box-sizing: border-box;
        padding: 0;
        margin: 0;
        vertical-align: top;
    }

    .st-marketplace-search
    {
        padding: 0 0 0 6px;
        width: 30%;

        input 
        {
            width: 100%;
            border:1px solid #ccc;
            background-image:none;
            background-color: #fafafa;
            -webkit-box-shadow: none;
            -moz-box-shadow: none;
            box-shadow: none;
            padding: 5px 10px;
        }
    }

    .st-marketplace-categories
    {
        width: 69%;
        li
        {
            display: inline-block;
            cursor: pointer;
            background: red;
            padding: 8px 20px;

            background: #f3f3f3;
            color: #23282d;
            box-shadow: 0 1px 0 #ccc;

            border-top: 1px solid darken(#f3f3f3, 10%);
            border-right: 1px solid darken(#f3f3f3, 10%);
            border-left: 1px solid lighten(#f3f3f3, 10%);
            font-size: 13px;
            transition: background-color ease 0.3s;

            &:first-child
            {
                border-left: 1px solid darken(#f3f3f3, 10%);
            }

            &.active
            {
                background-color: #a04c9c;
                color: #fff;
                border-top: 1px solid darken(#a04c9c, 10%);
                border-right: 1px solid darken(#a04c9c, 10%);
                border-left: 1px solid lighten(#a04c9c, 10%);
            }

            &.st-filter-bar-filter
            {
                display: none;
                border: none;
                background: transparent;
                box-shadow: none;
                text-decoration: underline;
                color: #a04c9c;
            }

            &.st-only-purchased-btn
            {
                color: #858585;
                clear: both;
                text-decoration: none;
                padding-left: 0;
                font-size: 12px;
                margin: 7px 0 0;

                i { width: 12px; font-size: 13px; vertical-align: middle; }

                &.active
                {
                    color: #19a160;
                    i::before { content: "\f046"; }
                }
            }
        }
    }
}

// show unlock bonus only when on bonus tab
.st-marketplace-categories li.active[data-filter="3"] + li.st-unlock-bonus-btn { display: inline-block; }
.st-marketplace-categories li.active[data-filter="1"] ~ li.st-only-purchased-btn { display: block; width: 200px; }

.st-markplace-bonus-dialog
{
    float:left;width: 100%;
    padding: 0 0 10px;

    .st-markplace-bonus-dialog-form
    {
        display: block; float: left; width: 50%;

        input 
        {
            min-width: 300px;
        }
    }

    .st-markplace-bonus-dialog-notice
    {
        display: block; float: right; width: 40%; text-align: center;
    }
}

.wbc_importer h3.theme-name{
    line-height: 1.3;
    border:none;
}

.wbc_importer .theme-actions{
   opacity: 1 !important;
}

.wbc_importer .theme-browser .theme .theme-screenshot img{
    max-width: none !important;
    width:100% !important;
}
.wbc_importer .theme-browser .theme:focus .more-details, 
.wbc_importer .theme-browser .theme:hover .more-details {
    opacity: 1;
}

.wbc_importer .theme-browser .theme .spinner {
    background-position: right center;
    font-size: 12px;
    height: 20px;
    margin-right: 0;
    padding-right: 25px;
    padding-top: 5px;
    width: auto;
}

.wbc_importer .importer-button{
    margin-top:0;
}

.wbc_importer #wbc-importer-reimport{
    margin-left:4px;
    display: none;
}

.wbc_importer .theme-browser .theme:hover #wbc-importer-reimport{
    display: inline-block;
}

.wbc-importer-info{
    background-color: #fff;
    border-left: 4px solid orange;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
    padding: 1px 12px;
}